/* ================================= 
  Base Layout Styles
==================================== */

/* ---- Layout Containers ---- */

.photo-container {
	text-align: center;
	margin-bottom: 2.5em;
}
/* ---- Page Elements ---- */

.photo-container img {
	max-width: 100%;
	position: relative;
	z-index: 100;
}

.photo {
	position: relative;
	line-height: 0;
}
/* ---- Photo Overlay ---- */

.photo-desc {
	color: #fff;
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	padding-left: 20px;
	padding-right: 20px;
	display: flex; /* For centering text inside .photo-overlay */
	flex-direction: column;
	justify-content: center;
	align-items: center;
	background: #2d3e50;
	line-height: 0;
}
/* ---- Float clearfix ---- */

.clearfix::after {
	content: " ";
	display: table;
	clear: both;
}

/* ================================= 
  Media Queries
==================================== */

@media (min-width: 0) and (max-width: 768px) {
	.container {
		padding: 0 1.225em 2.5em;
	}
}
@media (min-width: 769px) {
	.container {
		padding: 0 2.25em 3em;
	}
	.photo-container {
		width: 48%;
		float: left;
		margin-left: 4%;
	}
}
@media (min-width: 769px) and (max-width: 1024px) {
	.photo-container:nth-child(2n + 1) {
		margin-left: 0;
		clear: left;
	}
}
@media (min-width: 1025px) {
	.photo-container {
		width: 30.63973064%;
	}
	.photo-container:nth-child(3n + 1) {
		margin-left: 0;
		clear: left;
	}
}

/* ================================= 
  Button Transitions
==================================== */

.button {
	-webkit-transition: background 0.3s;
	transition: background 0.3s;
}
.button:hover {
	background: rgba(74, 137, 202, 1);
}

/* ================================= 
  Photo 3D Transforms & Transitions
==================================== */

.content {
	-webkit-perspective: 700px;
	        perspective: 700px;
	-webkit-perspective-origin: 50% 100%;
	        perspective-origin: 50% 100%;
}

.photo {
	-webkit-transition: -webkit-transform 1s cubic-bezier(0.55, -0.62, 0.27, 1.2);
	transition: -webkit-transform 1s cubic-bezier(0.55, -0.62, 0.27, 1.2);
	transition: transform 1s cubic-bezier(0.55, -0.62, 0.27, 1.2);
	transition: transform 1s cubic-bezier(0.55, -0.62, 0.27, 1.2), -webkit-transform 1s cubic-bezier(0.55, -0.62, 0.27, 1.2);
	-webkit-transform-style: preserve-3d;
	        transform-style: preserve-3d;
}

.photo:hover {
	-webkit-transform: rotate3d(1, 0, 0, 180deg);
	        transform: rotate3d(1, 0, 0, 180deg);
}

.side-a,
.side-b {
	-webkit-backface-visibility: hidden;
	        backface-visibility: hidden;
}

.side-b {
	-webkit-transform: rotateX(180deg);
	        transform: rotateX(180deg);
}

