	border-radius: 2px;
	-webkit-transition: all 0.5s ease 0s;
	transition: all 0.5s ease 0s;
}
.button-default:hover {
	background-color: #17a2b8;
	background-color: var(--main-bg-color);
	color: #ffffff;
}
