/* ================================= 
  Base Element Styles
==================================== */

a {
	text-decoration: none;
}
h1,
h2,
h3 {
	text-transform: uppercase;
}
h2 {
	font-size: 1.125em;
	color: #76abdd;
	font-weight: 600;
	margin: 0;
}
h3 {
	font-size: 1.3em;
	line-height: 1.25em;
	margin-top: 0;
	margin-bottom: 0.5em;
}
p {
	font-size: 0.875em;
	line-height: 1.4;
	margin: 0 0 1em;
}

/* ================================= 
  Base Layout Styles
==================================== */

/* ---- Layout Containers ---- */

.main-header {
	text-align: center;
	padding: 2.8em 0;
}
.photo-container {
	text-align: center;
	margin-bottom: 2.5em;
}
/* ---- Page Elements ---- */

.name {
	font-size: 1.65em;
	font-weight: 800;
	margin: 0 0 1.5em;
	line-height: 1;
}
.name span {
	font-weight: 300;
	margin-left: -7px;
}
.logo {
	width: 45px;
	margin-bottom: 0.4em;
	cursor: pointer;
}
.photo-container img {
	max-width: 100%;
	position: relative;
	z-index: 100;
}
.button {
	color: #fff;
	width: 75%;
	font-weight: 700;
	display: block;
	text-decoration: none;
	text-transform: uppercase;
	padding: 20px 0;
	border-radius: 0.5em;
	background: rgba(74, 137, 202, 0.8);
	margin: 0.3em 0 0;
}
.photo {
	position: relative;
	line-height: 0;
}
/* ---- Photo Overlay ---- */

.photo-desc {
	color: #fff;
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	padding-left: 20px;
	padding-right: 20px;
	display: flex; /* For centering text inside .photo-overlay */
	flex-direction: column;
	justify-content: center;
	align-items: center;
	background: #2d3e50;
	line-height: 0;
}
/* ---- Float clearfix ---- */

.clearfix::after {
	content: " ";
	display: table;
	clear: both;
}

/* ================================= 
  Button Transitions
==================================== */

.button {
	-webkit-transition: background 0.3s;
	transition: background 0.3s;
}
.button:hover {
	background: rgba(74, 137, 202, 1);
}

/* ================================= 
  Photo 3D Transforms & Transitions
==================================== */

.content-portfolio {
	-webkit-perspective: 700px;
	        perspective: 700px;
	-webkit-perspective-origin: 50% 100%;
	        perspective-origin: 50% 100%;
}

.photo {
	-webkit-transition: -webkit-transform 1s cubic-bezier(0.55, -0.62, 0.27, 1.2);
	transition: -webkit-transform 1s cubic-bezier(0.55, -0.62, 0.27, 1.2);
	transition: transform 1s cubic-bezier(0.55, -0.62, 0.27, 1.2);
	transition: transform 1s cubic-bezier(0.55, -0.62, 0.27, 1.2), -webkit-transform 1s cubic-bezier(0.55, -0.62, 0.27, 1.2);
	-webkit-transform-style: preserve-3d;
	        transform-style: preserve-3d;
}

.photo:hover {
	-webkit-transform: rotate3d(1, 0, 0, 180deg);
	        transform: rotate3d(1, 0, 0, 180deg);
}

.side-a,
.side-b {
	-webkit-backface-visibility: hidden;
	        backface-visibility: hidden;
}

.side-b {
	-webkit-transform: rotateX(180deg);
	        transform: rotateX(180deg);
}
/* ================================= 
  Media Queries
==================================== */

@media (min-width: 0) and (max-width: 768px) {
	.container-portfolio {
		padding: 0 1.225em 2.5em;
	}
}
@media (min-width: 769px) {
	.container-portfolio {
		padding: 0 2.25em 3em;
	}
	.photo-container {
		width: 48%;
		float: left;
		margin-left: 4%;
	}
}
@media (min-width: 769px) and (max-width: 1024px) {
	.photo-container:nth-child(2n + 1) {
		margin-left: 0;
		clear: left;
	}
}
@media (min-width: 1025px) {
	.photo-container {
		width: 30.63973064%;
	}
	.photo-container:nth-child(3n + 1) {
		margin-left: 0;
		clear: left;
	}
	.form-portfolio {
		display: flex;
	}
}

/* ================================= 
  WEB FONTS
==================================== */

/* ================================= 
  ELEMENT STYLES
==================================== */

h1,
h2,
ul {
	margin: 0;
	padding: 0;
}
h2 {
	color: #404040;
	font-size: 1.6em;
	margin-bottom: 1.25em;
}
ul {
	list-style-type: none;
}
a {
	text-decoration: none;
	color: #3b80d1;
}
hr {
	border: 0;
	height: 0;
	margin: 2.3em 0 2em 0;
	border-top: 1px solid rgba(0, 0, 0, 0.1);
}

/* FORM & BUTTON STYLES -------- */

.home a,
input,
.tab-menu {
	padding: 0.5em;
	font-size: 0.9em;
	margin-right: 10px;
	border: none;
	border-radius: 3px;
	border: solid 1px #ccc;
	outline: none;
}
.tab-menu {
	color: white;
	background: #2d3e50;
	padding-left: 1.5em;
	padding-right: 1.5em;
	cursor: pointer;
	border-color: rgba(0, 0, 0, 0.1);
	text-align: center;
}

/* ================================= 
  LAYOUT & PAGE STYLES
==================================== */

.container-portfolio {
	width: 95%;
	max-width: 780px;
	padding-bottom: 2em;
	margin: 2em auto 2em;
	background: #f3f4f5;
	border-radius: 0.5em;
	box-shadow: 0 3px rgba(0, 0, 0, 0.1);
	overflow: hidden;
}
header {
	background: #dbdcdd;
	margin-bottom: 2em;
}
.main-content-portfolio {
	padding: 1em 2em;
}
.group:after {
	content: "";
	display: table;
	clear: both;
}
.course {
	margin-bottom: 1em;
}
/* Portfolio Header */
.home h2 {
	margin-top: 2em;
}
/* IMAGES -------- */

.course-img {
	width: 70px;
}
.teacher-img {
	width: 100%;
	border-radius: 0.3em;
}
.not-found {
	text-align: center;
}

/* MAIN NAV -------- */

.main-nav-portfolio a {
	font-size: 0.95em;
	color: #222;
	text-align: center;
	text-transform: uppercase;
	display: block;
	padding: 1em;
	-webkit-transition: 0.2s;
	transition: 0.2s;
}
.main-nav-portfolio .tab-menu:hover,
.main-nav-portfolio .active {
	color: #fff;
	background: #3b80d1;
}

/* COURSE NAV -------- */

.course-nav li {
	display: inline-block;
}
.course-nav li + li {
	margin-left: 0.6em;
}
.course-nav a {
	font-size: 0.85em;
	color: #7d7d7d;
	padding: 0.2em 0.8em;
	border-radius: 0.25em;
	background: #eaebeb;
	-webkit-transition: 0.2s;
	transition: 0.2s;
}
.course-nav a:hover,
.course-nav a.active {
	color: #fff;
	background: #39add1;
}

/* ICONS -------- */

.icn-logo {
	background: #54606b;
	color: #fff;
}
.icn-logo i {
	font-size: 2.45em;
	line-height: 0;
	margin-top: 30px;
}
.icn-error {
	font-size: 3.2em;
	margin-bottom: 0.25em;
}

/* ================================= 
  MEDIA QUERIES
==================================== */

@media (min-width: 0) and (max-width: 768px) {
	.home a {
		border-color: transparent;
	}
	.icn-logo {
		display: block;
		width: 100%;
		text-align: center;
		padding-bottom: 4px;
	}
	.main-nav-portfolio a {
		border-top: 2px solid transparent;
		border-bottom: 2px solid transparent;
	}
	.main-nav-portfolio a:hover {
		border-color: rgba(0, 0, 0, 0.1);
	}
	.courses h2 {
		margin-bottom: 0;
	}
	.course-nav {
		display: block;
		margin: 1.5em 0 3.2em;
	}
	.home a,
	input,
	.tab-menu {
		display: block;
		width: 100%;
		margin-bottom: 1em;
	}
}
@media (min-width: 769px) {
	.media img,
	.media > div,
	.teacher {
		float: left;
	}
	.media > div {
		width: 85.1%;
		margin-left: 1.5em;
	}
	.teacher {
		width: 48.73%;
		margin-bottom: 1.5em;
	}
	.teacher + .teacher {
		margin-left: 2.54%;
	}
	.teacher:nth-child(2n + 3) {
		clear: left;
		margin-left: 0;
	}
	/* MAIN NAV -------- */
	.icn-logo {
		margin-right: auto;
		padding: 0 2em;
	}
	header,
	.main-nav-portfolio {
		display: flex;
		flex-wrap: wrap;
	}
	.main-nav-portfolio a {
		border-left: 3px solid transparent;
		border-right: 3px solid transparent;
		padding: 1.25em 1.5em;
	}
	.main-nav-portfolio a:hover {
		border-color: rgba(0, 0, 0, 0.1);
	}
	/* COURSE NAV -------- */
	.course-nav {
		float: right;
	}
}

